<template>
  <div>
    <app-header></app-header>
    <app-jumbo
        title="           "
        subTitle="                    "
        jumboActiveClass="text-right"
        titleClass="title-right"
        subTitleClass="sub-title-right"
        tempClass="active"
      jumboClass="jumbotron-lg"
    >
    </app-jumbo>
    <div class="wrapper">

      <app-confirm-input-number
          label=" "
          :placeHolder="getUserTel"
          inputType="tel"
          id="phoneNumber"
          backSrc="/login"
      ></app-confirm-input-number>
      <app-confirm-input-number
          label=" "
          :placeHolder="getUserId"
          inputType="text"
          id="idNumber"
          backSrc="/login"
      ></app-confirm-input-number>
      <div class="result-container">
        <app-result-box :title="getTitle" :subTitle="getSubTitle"></app-result-box>
        </div>
        <div class="btn-container">
          <router-link to="/appointment">
            <app-button :active="true" :label="getLabel"></app-button>
          </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

import Jumbotron from "../Jumbotron.vue";
import ConfirmInputNumber from "../confirmation/ConfirmInputNumber.vue";
import ResultBox from "./ResultBox.vue";
import Button from "../Button.vue";
import Header from "../Header.vue";

export default {
  data() {
    return {
      state: "accepted",
      // states could be :
      // accepted , un_authorize or signUp
    };
  },
  components: {
    appJumbo: Jumbotron,
    appConfirmInputNumber: ConfirmInputNumber,
    appButton: Button,
    appResultBox: ResultBox,
    appHeader: Header,
  },
  computed: {
    ...mapGetters({
      getUserTel: "getUserTelNumber",
      getUserId: "getUserIdNumber",
      getCaptcha: "getUserCaptchaCode",
      getStatus: 'getUserStatus'
    }),
    getLabel() {
      switch (this.getStatus) {
        case "accepted":
          return "   ";
        case "un_authorize":
          return "  ";
        case "signUp":
          return "    ";
        default:
          return "   ";
      }
    },
    getTitle() {
      switch (this.getStatus) {
        case "accepted":
          return "  ";
        case "un_authorize":
          return "       ";
        case "signUp":
          return "       ! ";
        default:
          return "       !";
      }
    },
    getSubTitle() {
      switch (this.getStatus) {
        case "accepted":
          return "                  ";
        case "un_authorize":
          return "                    ";
        case "signUp":
          return "               ";
        default:
          return "       !";
      }
    },
  },
};
</script>

